import React, { Component } from "react";

import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import { Form, Input, Select, Radio, Divider, Row, Col, Alert } from "antd";

const { Option } = Select;
// const { TextArea } = Input;

class CurrencyUpdateForm extends Component {
  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        this.props.submit(values);
      }
    });
  };

  render() {
    // console.log(this.props);
    const { getFieldDecorator, getFieldError } = this.props.form;
    const { currency } = this.props;
    const { loading } = currency;
    const data = currency.currency ? currency.currency : [];
    const check_is_used = currency.check_is_used ? currency.check_is_used : 0;
    // var phone = data.phone + "";
    // phone = phone.substring(2);
    const used = data.is_used;
    const error = currency.errorField ? currency.errorField : [];
    //console.log(data);

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    const radioGroupStyle = { 
      display: "flex", 
      width: "50%" 
    }

    const radioButtonStyle = { 
      flex: "1", 
      textAlign: "center" 
    }

    return (
      <Form layout="vertical" onSubmit={this.handleSubmit}>
        {used ? (
          <Row style={{ marginBottom: "20px" }}>
            <Col span={24}>
              <Alert
                message={<IntlMessages id="currencies.help" />}
                type="warning"
                showIcon
              />
            </Col>
          </Row>
        ) : (
          ""
        )}

        {/* <Divider orientation="left" style={{ fontSize: 14 }}>
          <IntlMessages id="currencies.add.subtitle.data" />
        </Divider> */}

        <div className="isoDetailDataFlex" style={{ padding: "0" }}>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 15px 0 0" }}>
            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="currencies.add.code" />}
              validateStatus={
                error.currency_code || getFieldError("currency_code")
                  ? "error"
                  : null
              }
              help={
                error.currency_code
                  ? error.currency_code
                  : getFieldError("currency_code")
              }
            >
              {getFieldDecorator("currency_code", {
                initialValue: data.currency_code,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  },
                  {
                    max: 3,
                    message: <IntlMessages id="maxLength3" />
                  }
                ]
              })(
                <Input
                  disabled={true}
                  //style={{ textTransform: "uppercase" }}
                />
              )}
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="currencies.add.name" />}
            >
              {getFieldDecorator("currency_name", {
                initialValue: data.currency_name,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  },
                  {
                    max: 50,
                    message: <IntlMessages id="maxLength50" />
                  }
                ]
              })(
                <Input
                  disabled={true}
                  //style={{ textTransform: "uppercase" }}
                />
              )}
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="currencies.add.isBase" />}
            >
              {getFieldDecorator("is_base", {
                initialValue: data.is_base ? data.is_base : 0
              })(
                <Radio.Group
                  style={radioGroupStyle}
                  buttonStyle="solid"
                  disabled={check_is_used ? true : used ? true : false}
                >
                  <Radio.Button style={radioButtonStyle} value={1}>
                    <IntlMessages id="yes" />
                  </Radio.Button>
                  <Radio.Button style={radioButtonStyle} value={0}>
                    <IntlMessages id="no" />
                  </Radio.Button>
                </Radio.Group>
              )}
            </Form.Item>
          </div>
        </div>

        <Divider orientation="left" style={{ fontSize: 14 }}>
          <IntlMessages id="currencies.add.subtitle.default" />
        </Divider>

        <div className="isoDetailDataFlex" style={{ padding: "0" }}>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 15px 0 0" }}>
            <Form.Item
              {...formItemLayout}
              label={
                <IntlMessages id="currencies.add.realizeGainAccount" />
              }
            >
              {getFieldDecorator("id_account_realize_gain", {
                initialValue: data.currency_account ? data.currency_account.id_account_realize_gain : null,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <Select
                  loading={currency.loadingFilterAccounts}
                  showSearch
                  optionFilterProp="children"
                  //disabled={used ? true : false}
                >
                  {this.props.currency.accounts.map((data, key) =>
                    data.id_account_type === "17"
                    ? (
                      <Option key={key} value={data.id_account}>
                        {data.account_code + " " + data.account_name}
                      </Option>
                    ) : null
                  )}
                </Select>
              )}
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={
                <IntlMessages id="currencies.add.realizeLossAccount" />
              }
            >
              {getFieldDecorator("id_account_realize_loss", {
                initialValue: data.currency_account ? data.currency_account.id_account_realize_loss : null,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <Select
                  loading={currency.loadingFilterAccounts}
                  showSearch
                  optionFilterProp="children"
                  //disabled={used ? true : false}
                >
                  {this.props.currency.accounts.map((data, key) =>
                    data.id_account_type === "18"
                    ? (
                      <Option key={key} value={data.id_account}>
                        {data.account_code + " " + data.account_name}
                      </Option>
                    ) : null
                  )}
                </Select>
              )}
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={
                <IntlMessages id="currencies.add.unrealizeGainAccount" />
              }
            >
              {getFieldDecorator("id_account_unrealize_gain", {
                initialValue: data.currency_account ? data.currency_account.id_account_unrealize_gain : null,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <Select
                  loading={currency.loadingFilterAccounts}
                  showSearch
                  optionFilterProp="children"
                  //disabled={used ? true : false}
                >
                  {this.props.currency.accounts.map((data, key) =>
                    data.id_account_type === "17"
                    ? (
                      <Option key={key} value={data.id_account}>
                        {data.account_code + " " + data.account_name}
                      </Option>
                    ) : null
                  )}
                </Select>
              )}
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={
                <IntlMessages id="currencies.add.unrealizeLossAccount" />
              }
            >
              {getFieldDecorator("id_account_unrealize_loss", {
                initialValue: data.currency_account ? data.currency_account.id_account_unrealize_loss : null,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <Select
                  loading={currency.loadingFilterAccounts}
                  showSearch
                  optionFilterProp="children"
                  //disabled={used ? true : false}
                >
                  {this.props.currency.accounts.map((data, key) =>
                    data.id_account_type === "18"
                    ? (
                      <Option key={key} value={data.id_account}>
                        {data.account_code + " " + data.account_name}
                      </Option>
                    ) : null
                  )}
                </Select>
              )}
            </Form.Item>
          </div>

          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 0 0 15px" }}>
            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="currencies.add.receivableAccount" />}
            >
              {getFieldDecorator("id_account_receivable", {
                initialValue: data.currency_account ? data.currency_account.id_account_receivable : null,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <Select
                  loading={currency.loadingFilterAccounts}
                  showSearch
                  optionFilterProp="children"
                  //disabled={used ? true : false}
                >
                  {this.props.currency.accounts.map((data, key) =>
                    data.id_account_type === "03" ? (
                      <Option key={key} value={data.id_account}>
                        {data.account_code + " (" + data.account_name + ")"}
                      </Option>
                    ) : null
                  )}
                </Select>
              )}
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="currencies.add.payableAccount" />}
            >
              {getFieldDecorator("id_account_payable", {
                initialValue: data.currency_account ? data.currency_account.id_account_payable : null,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <Select
                  loading={currency.loadingFilterAccounts}
                  showSearch
                  optionFilterProp="children"
                  //disabled={used ? true : false}
                >
                  {this.props.currency.accounts.map((data, key) =>
                    data.id_account_type === "09"
                    ? (
                      <Option key={key} value={data.id_account}>
                        {data.account_code + " (" + data.account_name + ")"}
                      </Option>
                    ) : null
                  )}
                </Select>
              )}
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={
                <IntlMessages id="currencies.add.invoiceDiscountAccount" />
              }
            >
              {getFieldDecorator("id_account_invoice_discount", {
                initialValue: data.currency_account ? data.currency_account.id_account_invoice_discount : null,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <Select
                  loading={currency.loadingFilterAccounts}
                  showSearch
                  optionFilterProp="children"
                  ////disabled={used ? true : false}
                >
                  {this.props.currency.accounts.map((data, key) =>
                    data.id_account_type === "14"
                    ? (
                      <Option key={key} value={data.id_account}>
                        {data.account_code + " " + data.account_name}
                      </Option>
                    ) : null
                  )}
                </Select>
              )}
            </Form.Item>
          </div>
        </div>

        <Form.Item {...tailFormItemLayout}>
          <Button
            type="primary"
            htmlType="submit"
            style={{ marginTop: "20px", marginLeft: "50px" }}
            loading={loading}
            //disabled={used && check_is_used ? true : false}
          >
            <IntlMessages id="button.submit" />
          </Button>
        </Form.Item>
      </Form>
    );
  }
}

const WrappedCurrencyUpdateForm = Form.create({ name: "update_currency" })(
  CurrencyUpdateForm
);

export default WrappedCurrencyUpdateForm;
