const actions = {
  getCurrencies: (page, search, filter) => ({
    type: 'GET_CURRENCIES_REQUEST',
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  createCurrency: () => ({
    type: 'CREATE_CURRENCY_REQUEST'
  }),
  storeCurrency: data => ({
    type: 'STORE_CURRENCY_REQUEST',
    payload: { body: data }
  }),
  editCurrency: id_currency => ({
    type: 'EDIT_CURRENCY_REQUEST',
    payload: { id_currency: id_currency }
  }),
  updateCurrency: (id_currency, data) => ({
    type: 'UPDATE_CURRENCY_REQUEST',
    payload: {
      id_currency: id_currency,
      body: data
    }
  }),
  showCurrency: id_currency => ({
    type: 'SHOW_CURRENCY_REQUEST',
    payload: { id_currency: id_currency }
  }),
  deactivateCurrency: id_currency => ({
    type: 'DEACTIVATE_CURRENCY_REQUEST',
    payload: { id_currency }
  }),
  activateCurrency: id_currency => ({
    type: 'ACTIVATE_CURRENCY_REQUEST',
    payload: { id_currency }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};

export default actions;
